import {
  Label,
  FormGroup,
  Input,
  Button,
  Text,
  Alert,
  Spinner,
} from '@sieloapp/components';
import { useForm, SubmitHandler } from 'react-hook-form';
import { hasKey } from '@sieloapp/shared/methods';
import { StyledWrap, StyledTitle, StyledDesc, StyledSmallText } from './style';
// AWS Amplify
import { Auth } from 'aws-amplify';
import { StyledRedirect } from '../signin-form/style';
import { Link, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';
import { useDispatch } from 'react-redux';
import { toggleUser } from '../../../src/redux/slices/user';
import xorCipher from '../../../src/helpers/crypt';

interface IFormValues {
  email: string;
  password: string;
  first_name: string;
  last_name: string;
}

const SignupForm = () => {
  const userPoolAWS = process.env.REACT_APP_COGNITO_USER_POOL_ID as string;
  const clientIdAWS = process.env.REACT_APP_COGNITO_CLIENT_ID as string;
  const recaptchakey = process.env.REACT_APP_RECAPTCHA_SITE_KEY as string;
  const [msg, setMessage] = useState('');
  const [signingUp, setsigningUp] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<IFormValues>();

  const onSubmit: SubmitHandler<IFormValues> = async (data) => {
    setsigningUp(true);
    try {
      console.log('==================onSubmit===================');
      const signUpResponse = await Auth.signUp({
        username: data.email,
        password: data.password,
        attributes: {
          email: data.email,
          name: data.first_name,
          family_name: data.last_name,
        },
      });
      setsigningUp(false);
      dispatch(
        toggleUser({
          userName: data.email,
          email: data.email,
          firstName: data.first_name,
          lastName: data.last_name,
          role: 'user',
          token: signUpResponse.userSub,
          company: undefined,
          verified: signUpResponse.userConfirmed,
          clientId: clientIdAWS,
          userPoolId: userPoolAWS,
          password: xorCipher(data.password),
        }),
      );
      navigate('/verify-account');
    } catch (error) {
      setsigningUp(false);
      const typedError = error as Error;
      setMessage(typedError.message);
    }
  };

  return (
    <StyledWrap>
      <StyledTitle>Create New Account</StyledTitle>
      <StyledDesc>It&apos;s free to signup and only takes a minute.</StyledDesc>
      {msg !== '' && <Alert color={'danger'}>{msg}</Alert>}
      <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
        <FormGroup mb="20px">
          <Label display="block" mb="5px" htmlFor="email">
            Email address
          </Label>
          <Input
            id="email"
            type="email"
            placeholder="Enter your email address"
            feedbackText={errors?.email?.message}
            state={hasKey(errors, 'email') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'email')}
            {...register('email', {
              required: 'Email is required',
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                message: 'invalid email address',
              },
            })}
          />
        </FormGroup>
        <FormGroup mb="20px">
          <Label display="block" mb="5px" htmlFor="password">
            Password
          </Label>
          <Input
            id="password"
            type="password"
            placeholder="Enter your password"
            feedbackText={errors?.password?.message}
            state={hasKey(errors, 'password') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'password')}
            {...register('password', {
              required: 'Password is required',
              minLength: {
                value: 6,
                message: 'Minimum length is 6',
              },
              // maxLength: {
              //   value: 10,
              //   message: 'Maximum length is 10',
              // },
            })}
          />
          <StyledSmallText>
            Must contain at least one number, special character, uppercase and
            lowercase letter more than 6 characters
          </StyledSmallText>
        </FormGroup>
        <FormGroup mb="20px">
          <Label display="block" mb="5px" htmlFor="first_name">
            Firstname
          </Label>
          <Input
            id="first_name"
            type="text"
            placeholder="Enter your firstname"
            feedbackText={errors?.first_name?.message}
            state={hasKey(errors, 'first_name') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'first_name')}
            {...register('first_name', {
              required: 'First Name is required',
              minLength: {
                value: 2,
                message: 'Minimum length is 2',
              },
            })}
          />
        </FormGroup>
        <FormGroup mb="20px">
          <Label display="block" mb="5px" htmlFor="last_name">
            Lastname
          </Label>
          <Input
            id="last_name"
            type="text"
            placeholder="Enter your Lastname"
            feedbackText={errors?.last_name?.message}
            state={hasKey(errors, 'last_name') ? 'error' : 'success'}
            showState={!!hasKey(errors, 'last_name')}
            {...register('last_name', {
              required: 'Last Name is required',
              minLength: {
                value: 2,
                message: 'Minimum length is 2',
              },
            })}
          />
        </FormGroup>
        <FormGroup mb="20px">
          <Text fontSize="12px">
            By clicking <strong>Create an account</strong> below, you agree to
            our terms of service and privacy statement.
          </Text>
        </FormGroup>
        <GoogleReCaptchaProvider reCaptchaKey={recaptchakey}>
          <Button type="submit" color="brand2" fullwidth>
            {signingUp ? (
              <div className="spin">
                <Spinner size="xs" color="light" />
              </div>
            ) : (
              'Sign Up'
            )}
          </Button>
        </GoogleReCaptchaProvider>
      </form>
      <StyledRedirect>
        Already registered? <Link to="/signin">Sign In here</Link>
      </StyledRedirect>
    </StyledWrap>
  );
};

export default SignupForm;
